import { Booking } from '@/models/booking.dto';
import { BookingListingService } from '@/services/api/booking_listing.service';
import { CommonService } from '@/services/api/common.service';
import { PropertyService } from '@/services/api/property.service';
import app_store from '@/stores/app.store';
import localizedWords from '@/stores/localization.store';
import { formatAmount } from '@/utils/utils';
import { Component, Host, Prop, State, h } from '@stencil/core';
import axios from 'axios';
import { differenceInCalendarDays, format } from 'date-fns';

@Component({
  tag: 'ir-booking-listing',
  styleUrl: 'ir-booking-listing.css',
  shadow: true,
})
export class IrBookingListing {
  @Prop() propertyid: number;
  @Prop() baseUrl: string;
  @Prop() headerShown: boolean = true;
  @Prop() footerShown: boolean = true;

  @State() isLoading = false;
  @State() token: string =
    'eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJleHAiOjE3MTY0NzAzMjYsIkNMQUlNLTAxIjoiZE1TL0hDV21NdW89IiwiQ0xBSU0tMDIiOiI5UStMQm93VTl6az0iLCJDTEFJTS0wMyI6Ilp3Tys5azJoTzUwPSIsIkNMQUlNLTA0IjoiQUVxVnRCMm1kWTg9IiwiQ0xBSU0tMDUiOiJFQTEzejA3ejBUcWRkM2gwNElyYThITHBnTWQvOWV5aSJ9.9hKPGjcvmTuRMAOE8BmXUY1NT3DDqSYDKgEokUV2N6U';
  @State() bookings: Booking[] = [];

  private bookingListingService = new BookingListingService();
  private commonService = new CommonService();
  private propertyService = new PropertyService();

  async componentWillLoad() {
    axios.defaults.baseURL = this.baseUrl;
    if (!this.propertyid) {
      throw new Error('missing property id');
    }
    if (this.token) {
      this.initializeServices();
      this.initializeApp();
    } else {
      const token = await this.commonService.getBEToken();
      if (token) {
        app_store.app_data.token = token;
      }
    }
  }
  initializeServices() {
    this.bookingListingService.setToken(this.token);
    this.propertyService.setToken(this.token);
  }
  async initializeApp() {
    try {
      this.isLoading = true;
      const [bookings] = await Promise.all([
        this.bookingListingService.getExposedGuestToken(this.propertyid),
        this.propertyService.getExposedProperty({ id: this.propertyid, language: app_store.userPreferences?.language_id || 'en' }),
      ]);
      this.bookings = bookings;
      console.log(this.bookings);
    } catch (error) {
      console.error(error);
    } finally {
      this.isLoading = false;
    }
  }
  handleAuthFinish(e: CustomEvent) {
    e.stopImmediatePropagation();
    e.stopPropagation();
    const { token, state } = e.detail;
    console.log(token, state);
    if (state === 'success') {
      this.token = token;
      this.initializeServices();
      this.initializeApp();
    }
  }
  getBadgeVariant(code: string) {
    if (code === '001') {
      return 'pending';
    } else if (code === '002') {
      return 'success';
    }
    return 'error';
  }
  render() {
    if (!this.token) {
      return (
        <Host>
          <main class="flex h-screen flex-col  justify-center">
            <div class="mx-auto w-full max-w-md px-4">
              {/* <ir-signin></ir-signin> */}
              <ir-auth enableSignUp={false} onAuthFinish={this.handleAuthFinish.bind(this)}></ir-auth>
            </div>
          </main>
        </Host>
      );
    }

    return (
      <Host>
        {this.headerShown && <ir-nav isBookingListing website={app_store.property?.space_theme.website} logo={app_store.property?.space_theme?.logo}></ir-nav>}
        <main class={' p-4'}>
          <div class="table-container mx-auto max-w-6xl overflow-x-hidden shadow-md">
            <div class="flex flex-col gap-2.5 px-[20px] py-[16px] md:flex-row md:items-center md:justify-between ">
              <h3>Bookings</h3>
              {/* <input type="text" placeholder="Search..." class="w-full max-w-sm rounded-md border border-solid border-gray-300 px-4 py-1.5 " /> */}
            </div>
            <div class="max-w-full overflow-x-auto ">
              <table class="table">
                <thead>
                  <tr class="table-header">
                    <th class="table-head">Status</th>
                    <th class="table-head">{localizedWords.entries.Lcz_BookingPreference}</th>
                    <th class="table-head">Booking date</th>
                    <th class="table-head">Check-in</th>
                    <th class="table-head">Duration</th>
                    <th class="table-head">Total price</th>
                    <th class="table-head sr-only">pay now</th>
                  </tr>
                </thead>
                <tbody>
                  {this.bookings?.map(booking => {
                    const totalNights = differenceInCalendarDays(new Date(booking.to_date), new Date(booking.from_date));
                    return (
                      <tr class="table-row" key={booking.booking_nbr}>
                        <td class="table-cell">{<ir-badge label={booking.status.description} variant={this.getBadgeVariant(booking.status.code)}></ir-badge>}</td>
                        <td class="table-cell">{booking.booking_nbr}</td>
                        <td class="table-cell">{format(new Date(booking.booked_on.date), 'dd-MMM-yyyy')}</td>
                        <td class="table-cell">{format(new Date(booking.from_date), 'EEE, dd MMM yyyy')}</td>
                        <td class="table-cell">
                          {totalNights} {totalNights > 1 ? 'nights' : 'night'}
                        </td>
                        <td class="table-cell">{formatAmount(booking.total, booking.currency.code)}</td>
                        <td class="table-cell"></td>
                      </tr>
                    );
                  })}
                </tbody>
              </table>
            </div>
            {/* <div class="flex items-center justify-between px-[20px] py-[16px] ">
              <ir-button variants="outline" label="Previous" haveLeftIcon>
                <ir-icons name="arrow_left" slot="left-icon" svgClassName="size-3"></ir-icons>
              </ir-button>
              <ir-button variants="outline" label="Next" haveRightIcon>
                <ir-icons name="arrow_right" slot="right-icon" svgClassName="size-3"></ir-icons>
              </ir-button>
            </div> */}
          </div>
        </main>
        {this.footerShown && <ir-footer></ir-footer>}
      </Host>
    );
  }
}
